@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Teko:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Teko:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC&family=Teko:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: black;
    margin: 0px;

}

.navbar a{
    text-decoration: none;
    color: white;
    font-family: 'Amatic SC', cursive;
    font-family: 'Teko', sans-serif;
    font-size: 2rem;
    padding: 20px;
    /* border: 1px solid red; */
    margin: 0;
    /* border-radius: 100px; */

    
}
 .navbar a:hover{
    /* background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%); */
    color: red;
    /* border-radius: 20px; */

 }
.header{
    text-align: center;
    font-size: 3rem;
    margin: 0px;
}
.header h2{
    font-family: 'Amatic SC', cursive;
    font-family: 'Teko', sans-serif;
    padding: 0 20px 20px;
    transition: 1s;

}
.header h2:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: 0.6s;
    cursor: pointer;
    color: red;


}

.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.cards{
    border-radius: 7px;
    padding: 20px;
    background-color: black;
    position: relative;
    overflow: hidden;
    box-shadow:  3px 5px rgba(0,0,0,0.7);
    margin: 10px;

}
.title{
    font-family: 'Amatic SC', cursive;
    font-family: 'Teko', sans-serif;
    text-align: center;
    color: white;
    transition: 0.5s

}
img{
    width: 400px;
    height: 300px;
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
    object-fit: cover;


}
.cards img:hover{
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
}

.cardDisc{
    position: absolute;
    background-color: rgba(0,0,0,0.5);
    color: white;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 1rem;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    transition: 0.7s ease-in-out;


}
.cards:hover .cardDisc{
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    transition: 0.7s ease-in-out;
}
.cards:hover .title{
    color: red;
    transition: 0.5s
}
.App {
  background-image: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
}
